#certificate {
  margin-left: 50px;
}

img {
  width: 120px;
  height: 80px;
}

fieldset {
  width: 200px;
  margin-left: 5px;
  font-size: 1.5em;
}

button {
  background-color: blue;
  /* 버튼 배경색 (파란색) */
  color: white;
  /* 버튼 텍스트 색 (흰색) */
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
  /* 변환 애니메이션 속도 설정 */
  margin-top: 10px;
  /* 위쪽 여백 설정 (원하는 여백 크기로 조정) */
  margin-bottom: 10px;
  /* 아래쪽 여백 설정 (원하는 여백 크기로 조정) */
  display: block;
  /* 버튼을 블록 요소로 표시 (수직 정렬을 위해) */
  border-radius: 5px;
  /* 모서리를 둥글게 설정 (원하는 반지름 크기로 조정) */
}

button:hover {
  background-color: yellow;
  /* 마우스 호버 시 버튼 배경색 (노란색) */
}