/* 표 전체 스타일 */
table {
  width: 100%;
  border: 1px solid black;
  border-collapse: collapse;
  text-align: center;
  font-size: 1.5em;
  background-color: whitesmoke;
  /* 원하는 색상으로 변경 가능 */
}

/* 표 제목 스타일 */
caption {
  font-size: 1.5em;
  font-weight: bold;
  text-align: center;
  background-color: #f0f0f0;
  padding: 10px;
  color: blue;
  /* 원하는 제목 색상으로 변경 가능 */
}

/* 표 셀 스타일 */
td,
th {
  width: 20%;
  border: 1px solid skyblue;
  padding: 10px;
}

tr {
  height: 30px;
}

.intern-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
}

#pledge {
  text-align: right;
  width: 100%;
  /* 변경: 폼 엘리먼트가 화면 너비를 100% 차지하도록 설정 */
  font-family: "Song Myung";
  margin-left: 0;
  /* 변경: 왼쪽 여백 제거 */
  font-size: 1.5em;
  color: #000;
  font-family: Inter;
  font-size: 40px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: #000;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.intern-buttons button {
  background-color: green;
  /* Default button color */
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 1.2em;
}

.intern-buttons button:hover {
  background-color: yellow;
  /* Button color on hover */
}